header img.logo {
  width: 14rem;
}

header .facetwp-search-wrap input {
  border-width: 0px;
}

header .facetwp-search {
    padding-right: 0;
}

header h1 {
  font-family: 'Loukia Neue Ultra Light';
  text-rendering: optimizelegibility;
  font-size: 1.1rem;
    letter-spacing: 0.23rem;
}
/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {

 header h1 {
  font-size: 1.35rem;
 }


}

header a:hover {
