}

header h1 span.highlight {
  font-family: 'Loukia Neue Regular';
  text-rendering: optimizelegibility;

}

header .navbar {
  padding-top: 1rem;
  padding-bottom: 0rem;
}

header .facetwp-facet {
    margin-bottom: 0px;
}


.mr-8 {
margin-right: 8rem!important;
}
