article .grid-item-landscape img {
  float: left;
  width: 665px; }

article .gallery-left {
  width: 50%; }

article .gallery-right {
  width: 50%; }

header img.logo {
  width: 14rem; }

header .facetwp-search-wrap input {
  border-width: 0px; }

header .facetwp-search {
  padding-right: 0; }

header h1 {
  font-family: 'Loukia Neue Ultra Light';
  text-rendering: optimizelegibility;
  font-size: 1.1rem;
  letter-spacing: 0.23rem; }

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  header h1 {
    font-size: 1.35rem; } }

header a:hover {
  text-decoration: none; }

header h1 span.highlight {
  font-family: 'Loukia Neue Regular';
  text-rendering: optimizelegibility; }

header .navbar {
  padding-top: 1rem;
  padding-bottom: 0rem; }

header .facetwp-facet {
  margin-bottom: 0px; }

.mr-8 {
  margin-right: 8rem !important; }

/* Radio */
.facetwp-radio {
  background: url(../images/menu_bullet_off.svg) 0 30% no-repeat !important; }

.facetwp-radio.checked {
  background-image: url(../images/menu_bullet.svg) !important; }

body#tinymce {
  margin: 12px !important; }
