article {
  padding-bottom: 5rem;
}

article .entry-meta {
  adding: 0.3rem 0 8rem 0;
}

article h2.entry-title {
  margin-top: 0.2rem;
}

.post_carousel-indicators {
}


.entry-indicators div {
  box-sizing: content-box;
-ms-flex: 0 1 auto;
flex: 0 1 auto;
width: 30px;
height: 3px;
margin-right: 3px;
margin-left: 3px;
text-indent: -999px;
cursor: pointer;
background-color: transparent;
background-clip: padding-box;
border-top: 10px solid transparent;
border-bottom: 10px solid transparent;
opacity: .5;
transition: opacity .6s ease;
    background: url(../images/menu_bullet_off.svg) 50% 50% no-repeat!important;
}


article .entry-indicators {
  padding-top: 1rem;
}

.carousel-control-next-icon {
    background-image: url(../images/carousel_next.svg);
}

.carousel-control-prev-icon{
    background-image: url(../images/carousel_prev.svg);
}

article .carousel-control-next, .carousel-control-prev {
  position: relative;
  display: inline-block;
  width: 1rem;
}

article .carousel-control-next-icon, .carousel-control-prev-icon {
    width: 5px;
    height: 9px;
}

article .entry-content {
  line-height: 1.5rem;
}

article .entry-tags {
  padding: 0 0 1rem 0;
}

article .grid-item-landscape img {
  float: left;
  width: 665px;
}

article .gallery-left {
  width: 50%;
}

article .gallery-right {
  width: 50%;
}
